<template>
  <div class="xdh-form-sub xdh-form-group" :class="classes" v-if="isActive" :style="styles">
    <div v-if="tool" class="xdh-form-sub__tool">
      <slot name="tool">
        <el-button @click="handleAdd" icon="el-icon-plus" size="mini"></el-button>
      </slot>
    </div>
    <div v-if="label" class="xdh-form-sub__label" :style="labelStyle">{{label}}
      <i v-if="collapsible"
         @click="handleCollapse"
         class="xdh-form-sub__handler el-icon-arrow-up" :class="{'is-caret':currentCollapsed}"></i></div>
    <transition name="el-zoom-in-top">
      <div v-show="!currentCollapsed" class="xdh-form-sub__content" :class="{'is-collapsed':currentCollapsed}">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
  /**
   * 
   * @module widgets/xdh-form-group
   *
   */
  import subForm from './mixins/subform'

  /**
   * 
   * @member slots
   * @property {string} - 
   * @property {string} tool tooltrue
   */

  export default {
    name: 'XdhFormGroup',
    mixins: [subForm],
    /**
     *  
     *  @member props
     *  @property {boolean} [block=true] falsewidth
     *  @property {string} [width] 
     *  @property {string} [height] 
     *  @property {boolean} [inline] 
     *  @property {String} [inlineSize]  inline 'large', 'medium', 'small'
     *  @property {string} [size]  medium / small / mini
     *  @property {string} [labelPosition]  left  right  label-width
     *  @property {string} [labelWidth]  '50px' Form  form-item  auto
     *  @property {string} [labelSuffix]  
     *  @property {String} [depend] 
     *  @property {*} [dependValue] 
     *  @property {string} [label] 
     *  @property {string} [collapsible=true] label
     *  @property {boolean} [collapsed=false] , collapsibletrue
     *  @property {boolean} [tool=false] 
     */
    props: {
      block: {
        type: Boolean,
        default: true
      },
      width: String,
      height: String
    },
    computed: {
      styles() {
        return {
          width: this.width,
          height: this.height,
          'vertical-align': this.block ? null : 'top',
          display: this.block ? 'block' : 'inline-block'
        }
      }
    }
  }
</script>
