<template>
  <div class="xdh-form-sub xdh-form-object" :class="classes" v-if="isActive">
    <div v-if="tool" class="xdh-form-sub__tool">
      <slot name="tool">
        <el-button @click="handleAdd" icon="el-icon-plus" size="mini"></el-button>
      </slot>
    </div>
    <div v-if="label" class="xdh-form-sub__label" :style="labelStyle">{{label}}
      <i v-if="collapsible"
         @click="handleCollapse"
         class="xdh-form-sub__handler el-icon-arrow-up" :class="{'is-caret':currentCollapsed}"></i></div>
    <transition name="el-zoom-in-top">
      <div v-show="!currentCollapsed" class="xdh-form-sub__content" :class="{'is-collapsed':currentCollapsed}">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
  /**
   * 
   * @module widgets/xdh-form-object
   *
   */
  import subForm from './mixins/subform'
  import {isEqual} from '@/utils/util'

  /**
   * 
   * @member slots
   * @property {string} - 
   * @property {string} tool tooltrue
   */

  export default {
    name: 'XdhFormObject',
    componentName: 'ElForm',
    mixins: [subForm],
    /**
     *  
     *  @member props
     *  @property {string|number} prop 
     *  @property {boolean} [inline] 
     *  @property {String} [inlineSize]  inline 'large', 'medium', 'small'
     *  @property {string} [size]  medium / small / mini
     *  @property {string} [labelPosition]  left  right  label-width
     *  @property {string} [labelWidth]  '50px' Form  form-item  auto
     *  @property {string} [labelSuffix]  
     *  @property {String} [depend] 
     *  @property {*} [dependValue] 
     *  @property {string} [label] 
     *  @property {string} [collapsible=true] label
     *  @property {boolean} [collapsed=false] , collapsibletrue
     *  @property {boolean} [tool=false] 
     */
    props: {
      prop: {
        type: [String, Number],
        required: true
      },
      label: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        currentModel: {}
      }
    },
    computed: {
      model() {
        if (this.parent.currentModel) {
          return this.parent.currentModel[this.prop] || {}
        }
        return {}
      },
      initModel() {
        const model = this.parent.model[this.prop] || {}
        return {...model}
      }
    },
    watch: {
      currentModel: {
        deep: true,
        handler(val, old) {
          /**
           * 
           * @event change
           * @param {Object} val 
           * @param {Object} old 
           */
          this.$emit('change', val, old)
          this.$set(this.parent.currentModel, this.prop, {...val})
        }
      },
      initModel: {
        deep: true,
        handler(val) {
          if (!isEqual(val, this.currentModel)) {
            this.setModel(val)
          }
        }
      }
    },
    methods: {
      setModel(val) {
        const model = {...val}
        this.currentModel = model
        this.$set(this.parent.currentModel, this.prop, model)
      }
    },
    created() {
      this.setModel(this.initModel)
    }
  }
</script>
